import { Component, OnInit } from '@angular/core';
// import { FormGroup,FormControl,Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { AppService } from '../app.service';
@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {
    form: FormGroup;
    loginForm: FormGroup;
  private formSubmitAttempt: boolean;
    visible:boolean = false
  isUserLoggedIn: boolean;


  public setUser(user){
    localStorage.setItem("user",JSON.stringify(user));
  }

  constructor(private router:Router, private fb: FormBuilder,private AppService:AppService) { }

  ngOnInit(): void {

        this.form = this.fb.group({
      userName: ['', Validators.required],
      password: ['', Validators.required],
      email: ['', Validators.required],
      mobile: ['', Validators.required],
    });

    this.loginForm = this.fb.group({
      userName: ['admin', Validators.required],
      password: ['admin', Validators.required],
    });
  }

  isFieldInvalid(field: string) {
    return (
      (!this.form.get(field).valid && this.form.get(field).touched) ||
      (this.form.get(field).untouched && this.formSubmitAttempt)
    );
  }

  onSubmit() {
    if (this.form.valid) {
      this.router.navigate(['/home']);
      // this.authService.login(this.form.value);
    }
    this.formSubmitAttempt = true;
  }

    Login() {
    if (this.loginForm.valid) {
      if(this.loginForm.value.userName=='admin' && this.loginForm.value.password=='admin' ){
        this.isUserLoggedIn=this.AppService.isLoggedIn(true);
         
         this.router.navigate(['/home']);
      }
      
      if(this.loginForm.value.userName=='student' && this.loginForm.value.password=='student' ){
        this.isUserLoggedIn=this.AppService.isLoggedIn(true);
        this.router.navigate(['/student']);
      }

    }
    this.formSubmitAttempt = true;
  }

    onclick()
  {
    
    // this.ReadMore = !this.ReadMore; //not equal to condition
    this.visible = !this.visible
  }

  signup(){
alert("under development please use username as student and password as student")
  }
  }


